function animation(ScrollMagic, Tween, Timeline) {
  "use strict"
  var NAMESPACE = "animation.gsap"

  ScrollMagic.Scene.addOption(
    "tweenChanges", // name
    false, // default
    function(val) {
      // validation callback
      return !!val
    }
  )
  // extend scene
  ScrollMagic.Scene.extend(function() {
    var Scene = this,
      _tween

    var log = function() {
      if (Scene._log) {
        // not available, when main source minified
        Array.prototype.splice.call(
          arguments,
          1,
          0,
          "(" + NAMESPACE + ")",
          "->"
        )
        Scene._log.apply(this, arguments)
      }
    }

    // set listeners
    Scene.on("progress.plugin_gsap", function() {
      updateTweenProgress()
    })
    Scene.on("destroy.plugin_gsap", function(e) {
      Scene.removeTween(e.reset)
    })

    /**
     * Update the tween progress to current position.
     * @private
     */
    var updateTweenProgress = function() {
      if (_tween) {
        var progress = Scene.progress(),
          state = Scene.state()
        if (_tween.repeat && _tween.repeat() === -1) {
          // infinite loop, so not in relation to progress
          if (state === "DURING" && _tween.paused()) {
            _tween.play()
          } else if (state !== "DURING" && !_tween.paused()) {
            _tween.pause()
          }
        } else if (progress != _tween.progress()) {
          // do we even need to update the progress?
          // no infinite loop - so should we just play or go to a specific point in time?
          if (Scene.duration() === 0) {
            // play the animation
            if (progress > 0) {
              // play from 0 to 1
              _tween.play()
            } else {
              // play from 1 to 0
              _tween.reverse()
            }
          } else {
            // go to a specific point in time
            if (Scene.tweenChanges() && _tween.tweenTo) {
              // go smooth
              _tween.tweenTo(progress * _tween.duration())
            } else {
              // just hard set it
              _tween.progress(progress).pause()
            }
          }
        }
      }
    }

    /**
     * Add a tween to the scene.
     * @param {(object|string)} TweenObject - A TweenMax, TweenLite, TimelineMax or TimelineLite object that should be animated in the scene. Can also be a Dom Element or Selector, when using direct tween definition (see examples).
     * @param {(number|object)} duration - A duration for the tween, or tween parameters. If an object containing parameters are supplied, a default duration of 1 will be used.
     * @param {object} params - The parameters for the tween
     * @returns {Scene} Parent object for chaining.
     */
    Scene.setTween = function(TweenObject, duration, params) {
      var newTween
      if (arguments.length > 1) {
        if (arguments.length < 3) {
          params = duration
          duration = 1
        }
        TweenObject = Tween.to(TweenObject, duration, params)
      }
      try {
        // wrap Tween into a Timeline Object if available to include delay and repeats in the duration and standardize methods.
        if (Timeline) {
          newTween = new Timeline({
            smoothChildTiming: true,
          }).add(TweenObject)
        } else {
          newTween = TweenObject
        }
        newTween.pause()
      } catch (e) {
        log(
          1,
          "ERROR calling method 'setTween()': Supplied argument is not a valid TweenObject"
        )
        return Scene
      }
      if (_tween) {
        // kill old tween?
        Scene.removeTween()
      }
      _tween = newTween

      // some properties need to be transferred it to the wrapper, otherwise they would get lost.
      if (TweenObject.repeat && TweenObject.repeat() === -1) {
        // TweenMax or TimelineMax Object?
        _tween.repeat(-1)
        _tween.yoyo(TweenObject.yoyo())
      }
      // Some tween validations and debugging helpers

      if (Scene.tweenChanges() && !_tween.tweenTo) {
        log(
          2,
          "WARNING: tweenChanges will only work if the TimelineMax object is available for ScrollMagic."
        )
      }

      // check if there are position tweens defined for the trigger and warn about it :)
      if (
        _tween &&
        Scene.controller() &&
        Scene.triggerElement() &&
        Scene.loglevel() >= 2
      ) {
        // controller is needed to know scroll direction.
        let triggerTweens = Tween.getTweensOf(Scene.triggerElement()),
          vertical = Scene.controller().info("vertical")
        triggerTweens.forEach(function(value, index) {
          let tweenvars = value.vars.css || value.vars,
            condition = vertical
              ? tweenvars.top !== undefined || tweenvars.bottom !== undefined
              : tweenvars.left !== undefined || tweenvars.right !== undefined
          if (condition) {
            return false
          }
        })
      }

      updateTweenProgress()
      return Scene
    }

    /**
     * Remove the tween from the scene.
     * This will terminate the control of the Scene over the tween.
     *
     * Using the reset option you can decide if the tween should remain in the current state or be rewound to set the target elements back to the state they were in before the tween was added to the scene.
     * @memberof! animation.GSAP#
     *
     * @example
     * // remove the tween from the scene without resetting it
     * scene.removeTween();
     *
     * // remove the tween from the scene and reset it to initial position
     * scene.removeTween(true);
     *
     * @param {boolean} [reset=false] - If `true` the tween will be reset to its initial values.
     * @returns {Scene} Parent object for chaining.
     */
    Scene.removeTween = function(reset) {
      if (_tween) {
        if (reset) {
          _tween.progress(0).pause()
        }
        _tween.kill()
        _tween = undefined
        log(3, "removed tween (reset: " + (reset ? "true" : "false") + ")")
      }
      return Scene
    }
  })
}

export default animation
